@font-face {
  font-family: 'Bayshore';
  src: local("Bayshore"), url("../fonts/Bayshore.woff") format("woff");
  font-weight: 100;
  font-style: normal;
}

.burger {
  display: inline-block;
  height: 25px;
  width: 25px;
  cursor: pointer;
  position: fixed;
  top: 60px;
  right: 15px;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
}

.burger__line {
  display: block;
  height: 6px;
  width: 100%;
  background-color: black;
  border: 1px solid #fff;
  border-radius: 3px;
  margin-bottom: 3px;
  transition: .5s;
}

.burger__line:last-child {
  margin-bottom: 0;
}

.lines .burger__line {
  background-color: black;
  transform-origin: left 50%;
}

.lines .burger__line:nth-child(1) {
  transform: rotate(45deg);
}

.lines .burger__line:nth-child(2) {
  visibility: hidden;
  opacity: 0;
}

.lines .burger__line:nth-child(3) {
  transform: rotate(-45deg);
}

.contacts__item {
  margin-bottom: 4px;
}

.contacts__item:last-child {
  margin-bottom: 0;
}

.contacts__link {
  color: #8f8f8f;
  font-size: 13px;
  font-weight: 300;
  font-family: 'Open Sans';
}

.feedback {
  margin-top: 100px;
}

.feedback .container {
  text-align: center;
}

.feedback__title {
  margin-bottom: 40px;
  text-align: center;
  overflow: hidden;
  margin: 0 50px 38px;
}

.feedback__title .title {
  display: inline-flex;
  align-items: center;
  margin: 0 17%;
  line-height: 36px;
  letter-spacing: 0.5px;
}

.feedback__title .title:after,
.feedback__title .title:before {
  content: '';
  display: inline-block;
  height: 1px;
  width: 100%;
  background-color: black;
}

.feedback__title .title:after {
  margin-right: -100%;
  margin-left: 60px;
}

.feedback__title .title:before {
  margin-left: -100%;
  margin-right: 60px;
}

.feedback__button {
  text-align: center;
}

.feedback__button .button {
  position: relative;
}

.feedback__button .button:after {
  content: '';
  display: inline-block;
  background: url(../../img/icon/heart.jpg) no-repeat;
  width: 15px;
  height: 13px;
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
}

.footer {
  margin-top: 109px;
}

.footer__wrapper {
  padding-top: 71px;
  border-top: 1px solid #e8e8e8;
}

.footer__row {
  display: flex;
  justify-content: space-between;
  margin-top: 121px;
}

.footer__col {
  width: 32%;
}

.footer__col--flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__text {
  font-family: 'Open Sans';
  color: #989898;
  font-weight: 13;
  font-weight: 300;
  line-height: 22px;
}

.footer__mission {
  font-family: 'Playfair Display';
  font-style: italic;
  font-size: 15px;
  color: #989898;
  display: inline-block;
  text-align: center;
  width: 63%;
  line-height: 24px;
}

.footer__painting {
  margin-top: 31px;
}

.footer__logo {
  margin-top: 36px;
}

.footer__insta {
  margin-left: 65px;
}

.footer__insta-title {
  font-size: 15px;
  font-family: 'Open Sans';
  color: #0b0b0b;
  font-weight: 700;
  margin-bottom: 20px;
}

.footer__contacts {
  text-align: right;
  margin-top: 103px;
  margin-right: 17px;
}

.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #373645;
  z-index: 5;
  opacity: 0;
  visibility: hidden;
  overflow-y: hidden;
  display: flex;
  align-items: center;
}

.fullscreen__menu {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fullscreen__menu .menu__list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.fullscreen__menu .menu__item {
  margin-right: 0;
  margin-bottom: 20px;
  opacity: .5;
  transition: 1s;
  transition-delay: .1s;
  transform: translateX(-150%);
}

.fullscreen__menu .menu__item:last-child {
  margin-bottom: 0;
}

.fullscreen__menu .menu__item:nth-child(even) {
  transform: translateX(150%);
}

.fullscreen__menu .menu__link {
  color: #fff;
  font-size: 30px;
}

.animate .menu__item {
  opacity: 1;
  transform: translateX(0);
}

.animate .menu__item:nth-child(even) {
  transform: translateX(0);
}

.is-active {
  visibility: visible;
  opacity: 1;
}

.instagram {
  margin-top: 100px;
}

.instagram__list {
  display: flex;
  margin-right: -30px;
  margin-bottom: -30px;
  flex-wrap: wrap;
  justify-content: center;
}

.instagram__item {
  height: 165px;
  width: 165px;
  margin-right: 30px;
  margin-bottom: 30px;
  transition: .4s;
  cursor: pointer;
  background-color: #e7a580;
}

.instagram__item:hover {
  opacity: .8;
}

.menu__list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu__item {
  margin-right: 45px;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  font-family: 'Open Sans';
  font-weight: 600;
  font-size: 15px;
  color: #0b0b0b;
  transition: .4s;
}

.menu__link:hover {
  color: grey;
}

.portfolio__list {
  display: flex;
  align-items: stretch;
}

.portfolio__item {
  width: 25%;
  min-height: 630px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  transition: .5s;
}

.portfolio__item:before {
  content: '';
  display: block;
  background-color: #484848;
  opacity: .45;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  transition: opacity .6s;
}

.portfolio__item:hover:before {
  background-color: #484848;
  opacity: 0;
}

.portfolio__item:hover .portfolio__button {
  animation: wrappButton .2s ease-in forwards;
  animation-direction: alternate;
}

.portfolio__item:hover .portfolio__link {
  background: rgba(72, 72, 72, 0.45);
}

.portfolio__img {
  width: 100%;
  height: 100%;
}

.portfolio__img-pic {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.portfolio__info {
  display: inline-block;
  text-align: center;
  font-family: 'Bayshore';
  font-weight: 400;
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.portfolio__link {
  display: inline-block;
  width: 68px;
  height: 68px;
  border: 2px solid #fff;
  border-radius: 50%;
  position: relative;
}

.portfolio__link:after {
  content: '';
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 15px solid transparent;
  border-left: 30px solid white;
  border-bottom: 15px solid transparent;
  position: absolute;
  top: 50%;
  left: 57%;
  transform: translate(-57%, -50%);
  opacity: 1;
}

.portfolio__names {
  font-size: 40px;
  white-space: nowrap;
  letter-spacing: 2.8px;
}

.portfolio__date {
  font-size: 24px;
  letter-spacing: 1.8px;
}

.portfolio__button {
  padding: 30px 0;
  width: 100%;
  background-color: #fff;
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
}

.portfolio__button .button {
  position: relative;
}

.portfolio__button .button:after {
  content: '';
  display: inline-block;
  background: url(../../img/icon/heart-red.png) no-repeat;
  width: 15px;
  height: 13px;
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
}

@keyframes wrappButton {
  from {
    bottom: -20%;
  }

  to {
    bottom: 0;
  }
}

.services {
  margin: 5% 0;
}

.services__list {
  display: flex;
  justify-content: center;
}

.services__item {
  margin-right: 30px;
  text-align: center;
  background: grey;
  padding: 15px;
}

.services__item:last-child {
  margin-right: 0;
}

.services__item--img {
  background: url(../../img/services/1.png) 100% 200% no-repeat;
}

.services__item--img .services__block {
  background-color: #fff;
}

.services__item--reverse {
  background: url(../../img/services/2.png) 0 0 no-repeat;
}

.services__item--color {
  background-color: #eaf2f9;
}

.services__block {
  background-color: #eaf2f9;
  border: 1px solid #fff;
  padding-top: 42px;
}

.services__icon {
  height: 35px;
  display: inline-block;
  margin-bottom: 14px;
}

.services__icon-pic {
  max-width: 100%;
  max-height: 100%;
}

.services__title {
  margin-bottom: 19px;
  padding: 0 26px;
}

.services__title.title {
  font-size: 20px;
  font-weight: 100;
  line-height: 26px;
}

.services__price {
  font-family: 'Roboto';
  font-size: 18px;
  font-weight: 100;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
}

.services__price:after {
  content: '';
  display: block;
  width: 55px;
  height: 1px;
  margin: 22px 0;
  background-color: black;
}

.services__descr {
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: 400;
  color: #232323;
  line-height: 27px;
  letter-spacing: 1px;
}

/*base styles*/

* {
  box-sizing: border-box;
  /*transition: .5s;*/
}

html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  /*font-family: 'Lato';*/
  font-size: 14px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: #fff;
}

.wrapper {
  position: relative;
  min-height: 100%;
  width: 100%;
  overflow: hidden;
  color: black;
}

.section {
  height: 100%;
  width: 100%;
}

.container {
  width: 1170px;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
}

.button {
  outline: none;
  border: 1px solid transparent;
  cursor: pointer;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: 100;
  color: #989799;
  transition: .4s;
  padding: 13px 26px;
}

.button--black {
  background-color: #0b0b0b;
}

.button--black:hover {
  background-color: grey;
  color: white;
}

.button--white {
  background-color: white;
  border-color: black;
  color: black;
}

.button--white:hover {
  background-color: grey;
  color: white;
}

.title {
  font-family: 'Playfair Display', serif;
  font-size: 24px;
  font-weight: 700;
  font-style: italic;
}

.overflow {
  overflow: hidden;
}

/*base end*/

@media screen and (max-width: 1200px) {
  .burger {
    opacity: 1;
    visibility: visible;
  }
}

@media screen and (max-width: 1000px) {
  .portfolio__list {
    flex-wrap: wrap;
  }

  .portfolio__item {
    width: 50%;
  }
}

@media screen and (max-width: 940px) {
  .services__list {
    flex-wrap: wrap;
  }

  .services__item {
    width: 45%;
  }

  .services__item:nth-child(2n) {
    margin-right: 0;
  }
}

@media screen and (max-width: 768px) {
  .feedback__title .title {
    margin: 0;
  }

  .footer__row {
    flex-direction: column;
    margin-top: 80px;
  }

  .footer__col {
    width: 100%;
    text-align: center;
    margin-bottom: 25px;
  }

  .footer__col:last-child {
    margin-bottom: 0;
  }

  .footer__insta {
    margin-left: 0;
  }

  .footer__contacts {
    margin-right: 0;
    text-align: center;
    margin-top: 60px;
  }

  .instagram__item {
    width: 100px;
    height: 100px;
  }

  .portfolio .container {
    padding: 0;
  }

  .container {
    padding: 0 3%;
  }
}

@media screen and (max-width: 480px) {
  .feedback__title {
    margin: 0 0 38px;
  }

  .feedback__title .title:after,
  .feedback__title .title:before {
    display: none;
  }

  .portfolio__item {
    width: 100%;
  }

  .services__item {
    width: 100%;
    margin-right: 0;
    margin-bottom: 20px;
  }

  .services__item:last-child {
    margin-bottom: 0;
  }

  .title {
    font-size: 19px;
  }
}