/*base styles*/

    *{
        box-sizing: border-box;
        /*transition: .5s;*/
    }

    html, body{
        padding: 0;
        margin: 0;
        height: 100%;
        width: 100%;
        /*font-family: 'Lato';*/
        font-size: 14px;

    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
    }

    a{
        text-decoration: none;
        color: #fff;
    }

    .wrapper{
        position: relative;
        min-height: 100%;
        width: 100%;
        overflow: hidden;
        color: black;
    }

    .section{
        height: 100%;
        width: 100%;
    }

    .container{
        width: 1170px;
        max-width: 100%;
        height: 100%;
        margin: 0 auto;


    }

    .button{
        outline: none;
        border:1px solid transparent;
        cursor: pointer;
        font-family: 'Roboto', sans-serif;
        font-size: 14px;
        font-weight: 100;
        color: #989799;
        transition: .4s;
        padding: 13px 26px;

        &--black{
            background-color: #0b0b0b;

            &:hover{
                background-color:grey;
                color: white;
            }
        }

        &--white{
            background-color: white;
            border-color: black;
            color: black;

            &:hover{
                background-color:grey;
                color: white;
            }
        }
    }

    .title{
        font-family: 'Playfair Display', serif;
        font-size: 24px;
        font-weight: 700;
        font-style: italic;
    }


    .overflow{
        overflow:hidden;
    }

/*base end*/


@media screen and (max-width: 768px){
    .container{
        padding: 0 3%;
    }
}

@media screen and (max-width: 480px){

    
    .title{
        font-size: 19px;
    }
}