.instagram{
	margin-top: 100px;

	&__list{
		display: flex;
		margin-right: -30px;
		margin-bottom: -30px;
		flex-wrap: wrap;
		justify-content: center;
	}

	&__item{
		height: 165px;
		width: 165px;
		margin-right: 30px;
		margin-bottom: 30px;
		transition: .4s;
		cursor: pointer;

		background-color: #e7a580;

		&:hover{
			opacity: .8;
		}
	}
}

@media screen and (max-width: 768px){
	.instagram{
		&__item{
			width: 100px;
			height: 100px;
		}
	}
}