.menu{

	&__list{
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__item{
		margin-right: 45px;

		&:last-child{
			margin-right:0;
		}
	}

	&__link{
		font-family: 'Open Sans';
		font-weight: 600;
		font-size: 15px;
		color: #0b0b0b;
		transition: .4s;

		&:hover{
			color:grey;
		}
	}
}