.footer{
	margin-top: 109px;

	&__wrapper{
		padding-top: 71px;
		border-top: 1px solid #e8e8e8;
	}

	&__row{
		display: flex;
		justify-content: space-between;
		margin-top: 121px;
	}

	&__col{
		width: 32%;

		&--flex{
			display: flex;
			flex-direction: column;
			align-items: center;
		}
	}

	&__text{
		font-family: 'Open Sans';
		color: #989898;
		font-weight: 13;
		font-weight: 300;
		line-height: 22px;
	}

	&__mission{
		font-family: 'Playfair Display';
		font-style: italic;
		font-size: 15px;
		color: #989898;

		display: inline-block;
	    text-align: center;
	    width: 63%;
	    line-height: 24px;
	}

	&__painting{
	    margin-top: 31px;
	}

	&__logo{
		margin-top: 36px;;
	}

	&__insta{
		margin-left: 65px;

		&-title{
			font-size: 15px;
			font-family: 'Open Sans';
			color: #0b0b0b;
			font-weight: 700;
			margin-bottom: 20px;
		}
	}

	&__contacts{
		text-align: right;
		margin-top: 103px;
    	margin-right: 17px;
	}
}

@media screen and (max-width: 768px){
	.footer{
		&__row{
			flex-direction: column;
			margin-top: 80px;
		}

		&__col{
			width: 100%;
			text-align: center;
			margin-bottom: 25px;

			&:last-child{
				margin-bottom:0;
			}
		}

		&__insta{
			margin-left: 0;
		}

		&__contacts{
			margin-right: 0;
			text-align: center;
			margin-top: 60px;
		}
	}
}