.services{
	margin: 5% 0;

	&__list{
		display: flex;
		justify-content: center;
	}

	&__item{
		margin-right: 30px;
		text-align: center;
		background: grey;
		padding: 15px;

		&:last-child{
			margin-right:0;
		}

		&--img{
			background: url(../../img/services/1.png) 100% 200% no-repeat;

			.services__block{
				background-color: #fff;
			}
		}

		&--reverse{
			background: url(../../img/services/2.png) 0 0 no-repeat;
		}

		&--color{
			background-color: #eaf2f9;
		}
	}

	&__block{
		background-color: #eaf2f9;
		border: 1px solid #fff;
		padding-top: 42px;
		
	}

	&__icon{
		height: 35px;
		display: inline-block;
		margin-bottom: 14px;

		&-pic{
			max-width: 100%;
			max-height: 100%;
		}
	}

	&__title{
		margin-bottom: 19px;
		padding: 0 26px;

		&.title{
			font-size: 20px;
			font-weight: 100;
			line-height: 26px;
		}
	}

	&__price{
		font-family: 'Roboto';
		font-size: 18px;
		font-weight: 100;

		display: inline-flex;
		flex-direction: column;
		align-items: center;

		&:after{
			content:'';
			display: block;
			width: 55px;
			height: 1px;
    		margin: 22px 0;
			background-color: black;
		}
	}

	&__descr{
		font-family: 'Roboto';
		font-size: 14px;
		font-weight: 400;
		color: #232323;
		line-height: 27px;
		letter-spacing: 1px;
	}
}

@media screen and (max-width: 940px){
	.services{
		&__list{
			flex-wrap: wrap;
		}

		&__item{
			width: 45%;

			&:nth-child(2n){
				margin-right: 0;
			}
		}
	}
}

@media screen and (max-width: 480px){
	.services{
		&__item{
			width: 100%;
			margin-right: 0;
			margin-bottom: 20px;

			&:last-child{
				margin-bottom:0;
			}
		}
	}
}