.feedback{
	margin-top: 100px;

	.container{
		text-align: center;
	}

	&__title{
		margin-bottom: 40px;
		text-align: center;
		overflow: hidden;
		margin: 0 50px 38px;

		.title{
			display: inline-flex;
			align-items: center;
    		margin: 0 17%;
    		line-height: 36px;
    		letter-spacing: 0.5px;
			
			&:after,&:before{
				content:'';
				display: inline-block;
				height: 1px;
    			width: 100%;
    			background-color: black;
			}

    		&:after{
    			margin-right: -100%;
    			margin-left: 60px;
    		}

    		&:before{
    			margin-left: -100%;
    			margin-right: 60px;
    		}
		}
	}

	&__button{
		text-align: center;

		.button{
			position: relative;
			

			&:after{
				content:'';
				display: inline-block;
				background: url(../../img/icon/heart.jpg) no-repeat;
				width: 15px;
				height: 13px;
				position: absolute;
				bottom: -8px;
				left: 50%;
				transform: translateX(-50%);
			}
		}
	}
}

@media screen and (max-width: 768px){
	.feedback{
		&__title{
			.title{
				margin: 0;
			}
		}
	}
}

@media screen and (max-width: 480px){
	.feedback{
		&__title{
			margin: 0 0 38px;

			.title{
				&:after, &:before{
					display:none;
				}
			}
		}
	}
}