.portfolio{
	&__list{
		display: flex;
		align-items: stretch;
	}

	&__item{
		width: 25%;
		min-height: 630px;
		overflow:hidden;
		position: relative;
		cursor: pointer;
		transition: .5s;

		&:before{
			content:'';
			display: block;
			background-color: #484848;
			opacity: .45;
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			transition: opacity .6s;
		}

		&:hover:before{
			background-color: #484848;
			opacity: 0;
		}

		&:hover{
			.portfolio__button{
				animation:wrappButton .2s ease-in forwards;
				animation-direction: alternate;
			}

			.portfolio__link{
				background: rgba(#484848, .45);
			}
		}
	}

	&__img{
		width: 100%;
		height: 100%;

		&-pic{
			height: 100%;
			width: 100%;
			object-fit: cover;
		}
	}

	&__info{
		display: inline-block;
		text-align: center;
		font-family: 'Bayshore';
		font-weight: 400;
		color: #fff;
		
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}

	&__link{
		display: inline-block;
		width: 68px;
		height: 68px;
		border:2px solid #fff;
		border-radius: 50%;
		position: relative;

		&:after{
			content:'';
			display: inline-block;
			width: 0;
			height: 0;
			border-top: 15px solid transparent;
		    border-left: 30px solid white;
		    border-bottom: 15px solid transparent;

		    position: absolute;
		    top: 50%;
		    left: 57%;
		    transform: translate(-57%, -50%);
		    opacity: 1;
		}
	}

	&__names{
		font-size: 40px;
		white-space: nowrap;
		letter-spacing: 2.8px;
	}

	&__date{
		font-size: 24px;
		letter-spacing: 1.8px;
	}

	&__button{
		padding: 30px 0;
		width: 100%;
		background-color: #fff;
		position: absolute;
		left: 0;
		right: 0;
		text-align: center;
		
		.button{
			position: relative;

			&:after{
				content:'';
				display: inline-block;
				background: url(../../img/icon/heart-red.png) no-repeat;
				width: 15px;
				height: 13px;
				position: absolute;
				bottom: -8px;
				left: 50%;
				transform: translateX(-50%);
			}
		}
	}
}

@keyframes wrappButton{
	from{
		bottom: -20%;
	}

	to{
		bottom: 0;
	}
}

@media screen and (max-width: 1000px){
	.portfolio{
		&__list{
			flex-wrap: wrap;
		}

		&__item{
			width: 50%;
		}
	}
}

@media screen and (max-width: 768px){
	.portfolio{
		.container{
			padding: 0;
		}
	}
}

@media screen and (max-width: 480px){
	.portfolio{
		&__item{
			width: 100%;
		}
	}
}